@value settings: '../../style/settings.css';
@value transition from settings;

.title {
  transition: transition;
  margin: 0;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 0px;
  transform: scale(0.7);
  width: 100%;
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-size: 2.2em;
  opacity: 0;
}

.titleEntering {
  transform: scale(0.7);
  opacity: 0;
}

.titleEntered {
  transform: scale(1);
  opacity: 1;
}



